import React, { useEffect } from 'react'
import { Route, Routes, Navigate } from "react-router-dom";
import { AppRoute, AuthRoute } from './AuthGraud';
import { useDispatch, useSelector } from 'react-redux';
import { login, logout } from '../redux/features/authSlice';
import { appapi } from '../api/apiClient';
import { CircularProgress, Backdrop } from '@mui/material'
import { decrypt } from '../utils/Crpyto';
import loadable from '@loadable/component';
import SignUp from '../pages/Auth/signUp/SignUp';
import ResetPassword from '../pages/Auth/resetPassword/ResetPassword';

//lazy Load
//Auth 
const Login = loadable(() => import('../pages/Auth/login/Login'))
const Register = loadable(() => import('../pages/Auth/register/Register'))
// const SignUp = loadable(() => import('../pages/Auth/signUp/SignUp'));
// const ResetPassword = loadable(() => import('../pages/Auth/resetPassword/ResetPassword'));
const ResetConfirm = loadable(() => import('../pages/Auth/resetConfirmPassword/ResetConfirm'));
const FinalizeSignup = loadable(() => import('../pages/Auth/finalizeSignup/FinalizeSignup'));
// Dashboard
const Dashboard = loadable(() => import('../pages/Layout/Dashboard'));
const EcoInitiatives = loadable(() => import('../pages/Layout/EcoInitiatives/EcoInitiatives'));
const UserManagement = loadable(() => import('../pages/Layout/usermanagment/UserManagement'));
const Integration = loadable(() => import('../pages/Layout/integration/Integration'));
const Payment = loadable(() => import('../pages/Layout/Payment/Payment'));
const Analytics = loadable(() => import('../pages/Layout/Analytics/Analytics'))

export default function RootNavigation() {

    const dispatch = useDispatch()
    const { isLoading } = useSelector(state => state.app)

    useEffect(() => {
        const value = localStorage.getItem('diz');

        if (value) {
            const decyptedData = decrypt(value);
            const userData = JSON.parse(decyptedData);
            dispatch(login(userData))
        }
    }, [dispatch])

    useEffect(() => {
        const tokenMonitor = (response) => {
            const { ok, status, config } = response;
            if (!ok && status === 403) {
                dispatch(logout())
            }
        }

        appapi.axiosInstance.interceptors.request.use(
            (config) => {
                // console.log(config);
                const data = localStorage?.getItem('diz') || null;
                if (data) {
                    const decyptedData = decrypt(data);
                    const userData = JSON.parse(decyptedData)
                    const token = userData?.accessToken
                    if (token) {
                        config.headers.Authorization = `Bearer ${token}`;
                    }
                }

                return config;
            },
            (error) => {
                Promise.reject(error);
            },
        );

        appapi.addMonitor(tokenMonitor);
    }, []);

    /**
     * Route has been Protected based on user Logged In or not.
     */
    return (
        <>
            <Routes>
                <Route path='/login' element={<AppRoute><Login /></AppRoute>} />
                <Route path='/signup' element={<AppRoute><SignUp /></AppRoute>} />
                <Route path='/verify/:verificationToken' element={<AppRoute><FinalizeSignup /></AppRoute>} />
                <Route path='/forgotPassword' element={<AppRoute><ResetPassword /></AppRoute>} />
                <Route path='/register/:verificationToken' element={<AppRoute><Register /></AppRoute>} />
                <Route path='/resetPassword/:verificationToken' element={<AppRoute><ResetConfirm /></AppRoute>} />
                <Route path='/' element={<AuthRoute><Dashboard /></AuthRoute>} >
                    <Route index element={<Navigate to="userManagement" replace />} />
                    <Route path='userManagement' element={<UserManagement />} />
                    <Route path='integrations' element={<Integration />} />
                    <Route path='analytics' element={<Analytics />} />
                    <Route path='ecoInitiatives' element={<EcoInitiatives />} />
                    <Route path='payments' element={<Payment />} />
                    <Route path='settings' element={<div className='main-Container'>Setting</div>} />
                </Route>
                <Route
                    path="*"
                    element={<Navigate to="/" replace />}
                />
            </Routes>
            <Backdrop
                sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.modal + 1 }}
                open={isLoading}
            >
                <CircularProgress color="inherit" />
            </Backdrop>
        </>

    )
}
