import './SignUp.scss';
import React, { useState } from 'react';
import diztextlogo from '../../../assets/images/diztextlogo.svg';
import wavesbg from '../../../assets/images/wavesbg.svg';
import VisibilityOutlinedIcon from '@mui/icons-material/VisibilityOutlined';
import VisibilityOffOutlinedIcon from '@mui/icons-material/VisibilityOffOutlined';
import mobilewavybg from '../../../assets/images/mobilewavybg.svg'
import * as yup from 'yup';
import { useFormik } from 'formik';
import { Stack, Typography, useMediaQuery, InputAdornment, IconButton, TextField, Button, FormControlLabel, Checkbox } from '@mui/material'
import { setLoading } from '../../../redux/features/appSlice';
import { colors } from '../../../utils/colors'
import { buttonStyle1 } from '../../../utils/commonStyle';
import { useNavigate } from 'react-router-dom';
import { appapi } from '../../../api/apiClient';
import { useDispatch } from 'react-redux';
import { showmessage } from '../../../utils/toastr';
import SocialCard from '../../../components/SocialCard';
import DetailCard from '../../../components/DetailCard';
import dizlogoMobile from '../../../assets/images/dizlogoMobile.svg';

/**
 * Validation Schema for register
 */
const validationSchema = yup.object({
    email: yup
        .string('Enter your Email')
        .matches(/^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/, 'Email is Invalid')
        .required('')
        .trim(),
    name: yup
        .string('Enter your Name')
        .min(3, 'Name must be minimum of 3 characters')
        .max(100, 'Name must be maximum of 100 characters')
        .required('')
        .trim(),
    organization: yup
        .string('Enter your Organization')
        .required('')
        .max(50, 'Organization must be maximum of 50 characters')
        .trim(),
    password: yup
        .string('Enter your Password')
        // .min(8, 'Password must be minimum of 6 characters')
        // .max(12, 'Password must be maximum of 12 characters')
        .matches(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,12}$/, 'Enter a password of minimum 8 and maximum 12 characters with a combination of capital letters, small letters, symbols, and numbers.')
        .required('Password is requried')
        .trim(),
    termsandCondition: yup.bool().oneOf([true], 'Accept Terms & Conditions is required')
})

export default function SignUp() {

    const dispatch = useDispatch()
    const navigate = useNavigate();
    const isMobile = useMediaQuery('(max-width:1000px)');

    const [visibility, setVisibility] = useState(false);
    const [formSubmitted, SetFormSubmitted] = useState(false);
    const [message, setMessage] = useState({
        status: '',
        msg: ''
    });
    const [showErrorEmail, setShowErrorEmail] = useState(undefined);
    const [showErrorName, setShowErrorName] = useState(undefined);
    const [showErrorOrganization, setShowErrorOrganization] = useState(undefined);
    const [showErrorPassword, setShowErrorPassword] = useState(undefined)

    const [data, setData] = useState({
        email: '',
        name: '',
        organization: '',
        password: '',
        termsandCondition: false
    })

    const formik = useFormik({
        enableReinitialize: true,
        initialValues: data,
        validationSchema: validationSchema,
        validateOnBlur: true,
        validateOnChange: true,
        onSubmit: (values) => {
            signUp(values)
        },
    })

    /**
    * 
    * @returns Redirect to initial page which is login
    */
    const onClickLoginRoute = () => navigate(`/login`)

    /**
     * @param {email, password} obj 
     * Sign Up new user
     */
    const signUp = async (obj) => {
        dispatch(setLoading(true))
        let payload = {
            emailId: obj.email,
            name: obj.name,
            password: obj.password,
            organization: obj.organization,
        }
        const response = await appapi.post(`/auth/signup`, payload)
        if (response.ok) {
            const data = response.data;
            dispatch(setLoading(false))
            if (data.status === 'success') {
                setMessage({
                    status: data?.status,
                    msg: data?.message
                })
                SetFormSubmitted(true)
            } else {
                setMessage({
                    status: data.status,
                    msg: data.message
                })
                showmessage(data.message, 2000, 'error')
            }
        } else {
            dispatch(setLoading(false))
            showmessage(`Something Went Wrong!`, 2000, 'error')
        }
    }

    const SignUpForm = ({ handleSubmit, values, handleChange, errors, touched, handleBlur, isValid, dirty }) => {
        return (
            <>
                <Typography className='fw-bold ff-Roboto createHeading' color={colors.primaryTxt}> Sign up </Typography>
                <form onSubmit={handleSubmit} style={{ minHeight: isMobile ? '670px' : 'initial' }}>
                    <Stack>
                        <Stack className='mt-17'>
                            <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={colors.blackMain}>Email</Typography>
                            <TextField
                                fullWidth
                                id='email'
                                name='email'
                                value={values.email.toLowerCase().replace(/^\s/g, '') || ''}
                                onChange={(e) => {
                                    if (e.target.value) {
                                        setShowErrorEmail(undefined)
                                    }
                                    handleChange(e);
                                }}
                                onBlur={(e) => {
                                    const re =
                                        /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/;
                                    let flag = re.test(e.target.value);
                                    if (e.target.value && !flag) {
                                        setShowErrorEmail('Email is Invalid')
                                    } else {
                                        setShowErrorEmail(undefined)
                                    }
                                    handleBlur(e);
                                }}
                                // error={touched.email && Boolean(errors.email)}
                                helperText={(touched.email) && showErrorEmail}
                                autoComplete='off'
                            />
                        </Stack>
                        <Stack className='mt-14'>
                            <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={colors.blackMain}>Name</Typography>
                            <TextField
                                fullWidth
                                id='name'
                                name='name'
                                value={values.name.replace(/^\s/g, '') || ''}
                                onChange={(e) => {
                                    if (e.target.value) {
                                        setShowErrorName(undefined)
                                    }
                                    handleChange(e);
                                }}
                                onBlur={(e) => {
                                    if (Boolean(errors.name)) {
                                        setShowErrorName(errors.name)
                                    } else {
                                        setShowErrorName(undefined)
                                    }
                                    handleBlur(e);
                                }}
                                // error={touched.name && Boolean(errors.name)}
                                helperText={(touched.name) && showErrorName}
                                autoComplete='off'
                            />
                        </Stack>
                        <Stack className='mt-14'>
                            <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={colors.blackMain}>Organization</Typography>
                            <TextField
                                fullWidth
                                id='organization'
                                name='organization'
                                value={values.organization.replace(/^\s/g, '') || ''}
                                onChange={(e) => {
                                    if (e.target.value) {
                                        setShowErrorOrganization(undefined)
                                    }
                                    handleChange(e);
                                }}
                                onBlur={(e) => {
                                    if (Boolean(errors.organization)) {
                                        setShowErrorOrganization(errors.organization)
                                    } else {
                                        setShowErrorOrganization(undefined)
                                    }
                                    handleBlur(e);
                                }}
                                // error={touched.organization && Boolean(errors.organization)}
                                helperText={(touched.organization) && showErrorOrganization}
                                autoComplete='off'
                            />
                        </Stack>
                        <Stack className='mt-14 passwordField'>
                            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={colors.blackMain}>Password</Typography>
                            </div>
                            <TextField
                                fullWidth
                                id='password'
                                name='password'
                                value={values.password.trim() || ''}
                                onChange={(e) => {
                                    const regex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,12}$/;
                                    let flag = regex.test(e.target.value);
                                    if (e.target.value && !flag) {
                                        setShowErrorPassword('Enter a password of minimum 8 and maximum 12 characters with a combination of capital letters, small letters, symbols, and numbers.')
                                    } else {
                                        setShowErrorPassword('')
                                    }
                                    handleChange(e);
                                }}
                                onBlur={handleBlur}
                                type={visibility ? 'text' : 'password'}
                                // error={touched.password && Boolean(errors.password)}
                                helperText={(touched.password) && showErrorPassword}
                                className={values.password ? 'helperTextStyleblack' : 'helperTextStylered'}
                                InputProps={{
                                    endAdornment: (
                                        <InputAdornment position='end'>
                                            {
                                                <IconButton onClick={() => setVisibility(!visibility)}>
                                                    {visibility ? <VisibilityOutlinedIcon /> : <VisibilityOffOutlinedIcon />}
                                                </IconButton>
                                            }
                                        </InputAdornment>
                                    )
                                }}
                            />
                        </Stack>
                        <Stack className='mt-17 checkboxStyle'>
                            <FormControlLabel
                                required
                                control={
                                    <Checkbox
                                        sx={{
                                            '&.Mui-checked': {
                                                color: colors.primaryTxt,
                                            },
                                        }}
                                        id='termsandCondition'
                                        name='termsandCondition'
                                        checked={!!values.termsandCondition}
                                        onChange={handleChange} />
                                }
                                label={<Typography className='fs-12 ff-Roboto fw-regular black'>I agree to the Terms of Service and Privacy Policy</Typography>}
                            />
                        </Stack>
                        <Button sx={buttonStyle1} disabled={!(dirty && isValid)} type='submit'>Sign up</Button>
                        <Stack direction='row' className='mt-16' justifyContent='center'>
                            <Typography className='ff-OpenSans fw-regular fs-12' color={colors.gray}>Already have an account? </Typography>
                            <Typography onClick={onClickLoginRoute} className='ff-OpenSans fw-bold fs-12' color={colors.primaryDark} style={{ marginLeft: '11px', cursor: 'pointer' }}>Login Here</Typography>
                        </Stack>
                    </Stack>
                </form>
            </>
        )
    }

    const SignUpFormSubmitted = () => {
        return (
            <>
                <Stack>
                    <Typography className='fw-bold ff-Roboto createHeading' color={colors.primaryTxt}> {message.status === 'success' ? 'Successful!' : 'Failed!'}  </Typography>
                    <Typography className='fs-12 ff-Roboto fw-regular' color={colors.blackMain} style={{ marginBottom: '6px' }}>
                        {message.msg}
                    </Typography>
                    <Button sx={{ ...buttonStyle1, textTransform: 'capitalize', marginBottom: '0' }} onClick={onClickLoginRoute} type='submit'>Go Back To Login</Button>
                </Stack>
            </>
        )
    }

    return (
        <div className='signUpContainer' style={{ padding: isMobile ? '16px' : '0', minHeight: isMobile ? 'auto' : '100vh' }}>
            {
                <>
                    {
                        formSubmitted ?
                            <>
                                {
                                    isMobile ?
                                        <div className='mobileViewContainer' style={{ minHeight: 'calc(100vh - 32px)' }}>
                                            <div style={{ backgroundImage: `url(${mobilewavybg})`, minHeight: 'calc(100vh - 32px)', backgroundPosition: 'center bottom', backgroundRepeat: 'no-repeat' }}>
                                                <div className='logoImg' style={{ marginLeft: '8px' }}>
                                                    <img alt='dizLogo' src={dizlogoMobile} />
                                                </div>
                                                <div className='innerContainer' style={{ marginTop: '240px' }}>
                                                    {SignUpFormSubmitted()}
                                                </div>
                                            </div>
                                        </div>
                                        :
                                        <Stack style={{ maxWidth: '1368px', margin: 'auto' }} direction='row' justifyContent='space-between'>
                                            <Stack className='leftStack' style={{ width: '100%' }}>
                                                <div className='logoImg'>
                                                    <img style={{ visibility: 'hidden' }} alt='dizLogo' src={diztextlogo} />
                                                </div>
                                                <div className='detailContainer' style={{ backgroundImage: `url(${wavesbg})` }}>
                                                </div>
                                                <Stack className='formSubmittedStyle'>
                                                    <div className='logoImg'>
                                                        <img alt='dizLogo' src={diztextlogo} />
                                                    </div>
                                                    <div style={{ marginTop: '22.916666666667vh' }}>
                                                        {SignUpFormSubmitted()}
                                                    </div>
                                                    <Stack className='socialContainer' style={{
                                                        margin: '30.208333333333vh auto 0 auto'
                                                    }} direction='row'>
                                                        {
                                                            <SocialCard />
                                                        }
                                                    </Stack>
                                                </Stack>
                                            </Stack>
                                        </Stack>
                                }
                            </>
                            :
                            <>
                                {
                                    isMobile ?
                                        <div className='mobileViewContainer' style={{ minHeight: 'calc(100vh - 32px)' }}>
                                            <div style={{ backgroundImage: `url(${mobilewavybg})`, minHeight: 'calc(100vh - 32px)', backgroundPosition: 'center bottom', backgroundRepeat: 'no-repeat' }}>
                                                <div className='logoImg' style={{ marginLeft: '8px' }}>
                                                    <img alt='dizLogo' src={dizlogoMobile} />
                                                </div>
                                                <div className='innerContainer' style={{ marginTop: '28px' }}>
                                                    {SignUpForm({ ...formik })}
                                                </div>
                                            </div>
                                        </div>
                                        :
                                        <Stack style={{ maxWidth: '1368px', margin: 'auto', minHeight: '768px' }} direction='row' justifyContent='space-between'>
                                            <Stack className='leftStack' style={{ width: '50%' }}>
                                                <div className='logoImg'>
                                                    <img alt='dizLogo' src={diztextlogo} />
                                                </div>
                                                <div className='detailContainer' style={{ backgroundImage: `url(${wavesbg})` }}>
                                                    <Stack className='detailsList'>
                                                        {
                                                            <DetailCard />
                                                        }
                                                    </Stack>
                                                    <Stack className='socialContainer' direction='row'>
                                                        {
                                                            <SocialCard />
                                                        }
                                                    </Stack>
                                                </div>
                                            </Stack>
                                            <Stack className='rightStack' style={{ width: '50%' }}>
                                                <div className='innerContainer'>
                                                    {SignUpForm({ ...formik })}
                                                </div>
                                            </Stack>
                                        </Stack>
                                }
                            </>
                    }
                </>
            }
            {
                isMobile ?
                    <Stack className='footer' direction='row' justifyContent='space-between'>
                        <Stack className='socialContainer' direction='row'>
                            {
                                <SocialCard />
                            }
                        </Stack>
                        <Typography className='fs-10 ff-Roboto fw-medium black dizcopyrights'>© 2022 Do It Zero Inc.</Typography>
                    </Stack>
                    :
                    <Typography className='fs-10 ff-Roboto fw-medium black dizcopyrights'>© 2022 Do It Zero Inc.</Typography>
            }
        </div>
    )
}
